.card-list {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 36px;
  max-width: 1010px;
  margin: 0 auto;
  padding: 36px 0;
}
.wrapper {
  max-width: 1010px;
  margin: 0 auto;
  background: #ffffff;
  padding: 20px;
  display: flex;
  flex-direction: column;
  row-gap: 15px;
}

input {
  width: 938px;
  border: 1px solid rgba(217, 217, 217, 1);
  font-family: Ayuthaya, sans-serif;
  margin: 0 auto;
  display: flex;
  font-size: 25px;
  padding: 9px 0 9px 12px;
}

.ant-pagination {
  display: flex;
  justify-content: center;
}
